.App {
  max-width: 100%;
  height: 100vh;
  background: linear-gradient(90deg, #8159C6 0%, #6C7CFF 100%);
}

.container {
  max-width: 1250px;
  width: 100%;
  min-height: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

@media (max-width: 770px) {
  .container {
    padding: 0 8px;
  }
}

@media (max-width: 350px) {
  footer {
    display: none;
  }
}
